import React from 'react';
import logo from './logo.svg';
import './App.css';

import { Article } from './hypersane/hypersane';
import { getHypersaneContent } from './hypersane/aeon-hypersane';
import { getHypersaneContentGraphical } from './hypersane/aeon-hypersane-graphical';


class App extends React.Component{
    constructor(props){
        super(props)

        this.state = {
            graphical: true,
        }
    }

    switchView = () => { 
        this.setState(prev => ({ graphical: !prev.graphical })) 
    }

    render(){

        const hypersaneContent = this.state.graphical ? getHypersaneContentGraphical() : getHypersaneContent();
        
        return (
            <div className="App">

                <Toggle onToggle={ this.switchView }></Toggle>

                <Article graphical={ this.state.graphical }>
                    { hypersaneContent }
                </Article>
            </div>
        );
    }
}


const Toggle = ( props ) =>{

    return (
        <div style={{width: '100%', height: '30px', display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
            <input type="checkbox" onClick={ props.onToggle } defaultChecked/>
            { 'extended view' }
        </div>
    )
}

export default App;




